@import "../../assets/styles/global.scss";

.toggle{
    width: 50px;
    height: 25px;
    border-radius: 20px;
    border: 1px solid $accent-color-2 ;
    background-color: $background-color-2;
    position: fixed;
    top: 10px;
    left: 10px;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.toggle.dark-mode{
    background-color: $background-color-dark-1;
}

.toggle-icon{
    width: 15px;
    height: 15px;
}

.toggle-button{
  width: 25px;
  height: 25px;
  border-radius: 50%;  
  background-color: $accent-color-2;
  position: absolute;
  cursor: pointer;
 
}